import React from 'react'
import styled from 'styled-components'
import TitleComp from '../../component/TitleComp'
import t1 from '../../assets/teacher01.jpg'
import t2 from '../../assets/teacher02.jpg'
import t3 from '../../assets/teacher03.jpg'
import t4 from '../../assets/teacher04.jpg'

const HotClassCss = styled.div`
  .container{
      padding:80px 0px;
  }
  .classCards{
    display: flex;
    justify-content:space-between;
    margin-top:48px;
  }
  .classCard{
    background-color:#2D2D2D;
    border-radius:24px;
    max-width:306px;
    padding:24px 0px 16px 0px;
    .user{
      text-align:center;
      img{
        height:100px;
        width:100px;
        border-radius:50px;
      }
      .name{
        margin-top:8px;
        display:block;
      }
    }
    .text{
      margin-top:24px;
      padding:0px 24px;
      .cardTitle{
        min-height:58px;
        max-height:58px;
      }
      .language{
        margin-top:8px;
      }
      .context{
        display:block;
        margin-top:8px;
      }
    }
    hr{
      margin-top:32px;
      color:#C1C1C1
    }
    .cost{
      margin-top:16px;
      text-align:center;
      .money{
        margin-left:8px;
        font-family:'Azeret Mono';
      }
    }
  }
  .classBtn{
    text-align:center;
    margin-top:48px;
    .classList{
      border-radius:8px;
      color:white;
      padding:8px 32px;
    }
  }

  @media (max-width: 1400px) {
    .classCards{
      row-gap:24px;
      flex-flow:wrap;
    }
    .classCard{
      max-width:calc(50% - 12px);
    }
  }
  @media (max-width:576px) {
    .container {
      padding:40px 12px;
    }
    .classCards{
      margin-top:24px;
    }
    .classCard{
      max-width:100%;
      .text{
        .cardTitle{
          min-height:auto;
          max-height:auto;
        }
      }
    }
    .classBtn{
      margin-top:24px;
    }
  }
`

const data = [{ photo: t1, name: 'Belinda', title: ' - HTML', language: '', text: ' HTML ...', money: 1600 },
{ photo: t2, name: 'Guava', title: '2022 Python ', language: '', text: 'Python...', money: 2200 },
{ photo: t3, name: 'Joe', title: ' Line Bot APP ', language: '', text: 'Line Bot ...', money: 1800 },
{ photo: t4, name: 'Lina', title: 'Linux ', language: '', text: 'LinuxUNIX...', money: 1100 },
]

interface DataProps {
  photo: string;
  name: string;
  title: string;
  language: string;
  text: string;
  money: number;
}

const Card: React.FC<DataProps> = (data) => {
  return (
    <div className="classCard">
      <div className="user">
        <img src={data.photo} alt="" />
        <span className="name">{data.name}</span>
      </div>
      <div className="text">
        <div className="cardTitle s20 titleWord">{data.title}</div>
        <h6 className="language fontColorGreen">{data.language}</h6>
        <span className="context">{data.text}</span>
      </div>
      <hr />
      <div className="cost">
        <span className="time">50</span>
        <span className="money s32">{`NT$${data.money}`}</span>
      </div>
    </div>
  )
}

export default function HotClass() {
  return (
    <HotClassCss className='globalBGBlack'>
      <div className="container s16">
        <TitleComp title={['']} />
        <div className="classCards">
          {data.map((ele, idx) => (<Card {...ele} key={idx} />))}
        </div>
        <div className='classBtn'><button className='classList linearLR s18'></button></div>
      </div>

    </HotClassCss>
  )
}
